import React from "react"
import "../../components/recommended.css"
import Post from "../../components/post"
import Header from "../../components/header"
import Navigation from "../../components/navigation"
import Layout from "../../components/layout"
import arr from "./searchEvents"
import { graphql, useStaticQuery } from "gatsby"
import { useState } from "react"
import { Router, Link, navigate } from "@reach/router"
import { isUndefined } from "util"
function Recommended(props) {
  const data = useStaticQuery(graphql`
    query MyQuery10 {
      site {
        siteMetadata {
          postInfo {
            password
            username
            posts {
              comments
              dateAndTime
              description
              location
              tags
            }
          }
        }
      }
    }
  `)

  const arr =
    props.location.state.data !== undefined
      ? ["", "", "", "", ""]
      : props.location.state.data
  console.log("arr", arr)
  const posts = data.site.siteMetadata.postInfo
  console.log(posts)
  let flag = true
  for (let i = 0; i < 5; i++) if (arr[i] !== "") flag = false
  if (flag) console.log("ne")
  let organizer
  if (!flag) {
    for (let i = 0; i < posts.length; i++) {
      if (arr[4] === posts[i].username) organizer = posts[i].posts
    }

    let postData = []
    console.log(organizer)
    for (let i = 0; i < organizer.length; i++) {
      if (
        (arr[0] === organizer[i].location || arr[0] === "") &&
        (arr[1] === organizer[i].date || arr[1] === "") &&
        (arr[2] === organizer[i].time || arr[2] === "") &&
        (arr[3] === JSON.stringify(organizer[i].tags) || arr[3] === "")
      ) {
        console.log("json", JSON.stringify(arr[3]) === "")
        postData.push(organizer[i])
      }
      console.log("json", JSON.stringify(arr[3]) === "")
    }
    console.log("ll", postData)
    return (
      <div class="reContent">
        <Layout>
          {postData.map(user => (
            <Post data={user} />
          ))}
        </Layout>
      </div>
    )
  }
}

export default Recommended
